import React from 'react'
import s from './MyPosts.module.css'
import Post from './Post/Post'
import { Field, reduxForm } from 'redux-form'
import { required, maxLengthCreator } from '../../../Utils/Validators/validators'
import { Textarea } from '../../Common/Preloader/FormControl/FrontControls'
// import { addPostActionCreator, updateNewPostText } from '../../../Redux/Profile-reducer'

const maxLength10 = maxLengthCreator(10)

const MyPosts = React.memo(props => {
    let postsData = props.posts
        .map(p => <Post message={p.message} key={p.id} counts={p.likesCount} />)

    let addPost = (values) => {
        props.addPost(values.newPostText)
    }

    return <div className={s.postsBlock}>
        <h3>My post</h3>
        <div>
            <AddMessageFormRedux onSubmit={addPost} />
        </div>
        <div className={s.posts}>
            {postsData}
        </div>
    </div>
}
)

const AddNewPostForm = (props) => {
    return (
        < form onSubmit={props.handleSubmit}>
            <div>
                <Field component={Textarea} name="newPostText" placeholder="enter your post" validate={[required, maxLength10]} />
            </div>
            <div><button>Text</button></div>
        </form>
    )
}

const AddMessageFormRedux = reduxForm({ form: 'newPostText' })(AddNewPostForm)

export default MyPosts